html {
  font-family: Roboto, serif;
}

body {
  margin: 0;
}

p {
  margin: 0;
  padding: 0;
}

.product-card {
  box-sizing: border-box;
  border: 1px solid #fff;
  border-radius: 5px;
  width: 200px;
  padding: 32px 16px 16px;
}

.product-card__img {
  width: 160px;
  height: 134px;
  margin: 0 auto 40px;
  display: block;
}

.product-card__name {
  color: #060b35;
  height: 36px;
  margin: 0 0 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.product-card__code {
  color: #616070;
  margin: 0 0 16px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.product-card__stars {
  justify-content: space-between;
  align-items: normal;
  margin-bottom: 24px;
  display: flex;
}

.reviews {
  text-align: right;
  color: #060b35;
  height: 14px;
  margin-top: 2px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.price {
  justify-content: space-between;
  margin-bottom: 16px;
  display: flex;
}

.price__text {
  color: #616070;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.price__value {
  text-align: right;
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.product-card__button {
  box-sizing: border-box;
  color: #fff;
  text-transform: uppercase;
  background-color: #00acdc;
  border: 1px solid;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  display: flex;
}

.product-card__button:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid;
}
